export default {
	
	props:{
		items:{
			type: Array
		},
		name:{
			type: String,
			default: '测试'
		},
		star:{
			type: String,
			default: 'false'
		},
		firstLabel:{
			type: String
		}
	},
	data() {
		return {
			index: -1
		};
	},
	onReady:function(){
		this.items.forEach((item,index)=>{
			if (item.id == this.firstLabel){
				this.index = index
				
			}
		})
	},
	methods:{
		itemChange:function(e){
			
			let index = e.detail.value
			this.index = index
			if (this.index == -1){
				this.index = 0
			}
			this.$emit("mychange",this.items[this.index].id)
		}
	}
}
